import React, { useState, useEffect } from 'react';
import '../css/index.css';
import CaretR from '../Caret_R.svg'
import CaretL from '../Caret_L.svg'

function Carousel(props) {
  const [count, setCount] = useState(0);
  useEffect(() => {
    if (count > 10) setCount(1)
    if (count < 1) setCount(10)
  })

  return (
    <div id="Carousel" >
      <button onClick={() => {setCount(count - 1)}} >
        <img src={CaretL} width={"10px"}/>
      </button>
      <button onClick={() => {setCount(count + 1)}} style={{right:"0"}}>
        <img src={CaretR} width={"10px"}/>
      </button>
      <div className='Scrolled'>
        <div style={{backgroundColor: "grey", paddingTop: "15vh"}}>
          PICTURE {count}
        </div>
        <div style={{backgroundColor: "green"}}>
          ABOUT ME {count}
        </div>
        <div style={{backgroundColor: "red"}}>
          CONTACT {count}
        </div>
      </div>
    </div>
  );
}

export default Carousel;